import React from 'react';
import styles from './LoadingScreen.module.css';

const LoadingScreen = ({ isVisible }) => {
  return (
    <div className={`${styles.loadingScreen} ${!isVisible ? styles.fadeOut : ''}`}>
      <div className={styles.textContainer}>
        <p className={styles.loadingText}>
          &gt; INICIALIZANDO SISTEMA...
        </p>
        <p className={`${styles.loadingText} ${styles.delay1}`}>
          &gt; CARREGANDO MÓDULOS...
        </p>
        <p className={`${styles.loadingText} ${styles.delay2}`}>
          &gt; CONECTANDO AO PORTFÓLIO
        </p>
        <p className={`${styles.loadingText} ${styles.delay3}`}>
           DE KLEISON VITORIANO...
        </p>
        <p className={`${styles.loadingText} ${styles.delay3} ${styles.blink}`}>
          &gt; BEM-VINDO_
        </p>
      </div>
    </div>
  );
};

export default LoadingScreen;